<template>
  <div class="compile-errors-molecule">
    <div class="compile-errors-molecule__close">
       <i @click="clearCompileErrors" class="el-icon-close"></i>
    </div>
    <div class="compile-errors-molecule__errors lc-custom-scroll">
      <table>
        <tr>
          <th width="200">Item</th>
          <th width="200">Block</th>
          <th width="60">Type</th>
          <th>Message</th>
        </tr>
        <tr v-for="error of compileErrors" :key="error.blockId">
          <td>{{getEditableName(error.editableId)}}</td>
          <td>{{error.caption}}</td>
          <td>{{error.type}}</td>
          <td>{{error.message}}</td>
        </tr>
      </table>
    </div>
  </div>
</template>

<script>
import CompileErrorsMolecule from './CompileErrorsMolecule.hook'
const hook = new CompileErrorsMolecule();

export default {
  name: 'CompileErrorsMolecule',
  beforeCreate() { hook.hook(this)},
  methods: hook.methods("clearCompileErrors","getEditableName"),
  props: { },
  data() {
      return { }
  },
  computed: hook.getters("compileErrors","openedDocument")
}

</script>

<style scoped lang="scss">

.compile-errors-molecule {
    background-color: #333333;
    height:100%;
    
    &__errors {
      margin:5px;
      height: calc(100% - 20px);
      overflow-y: auto;
      table, th, td {
        border: 1px solid black;
      }
      
      table {
        border-collapse: collapse;
        width:100%;
        tr{
          th {
            background-color: #353535;
            color: rgb(180, 179, 179);
            text-align: left;
          }
          td {
            background-color: #494949;
          }
        }
      }
    }

    &__close{
      width:100%;
      background-color: #1E1E1E;
      height:20px;
    
      text-align: right;
      i:hover {
        opacity: 0.5;
      }

      i {
        cursor: pointer;
        font-size: 20px;
        transition: 0.3s;
      }
    }
}

</style>
