import {AtomicDesign} from '../../../../../Frontend/Core/AtomicDesign';
import {EDITABLE_TYPES} from '../../../../../Common/VariousTypes';

export default class CompileErrorsMolecule extends AtomicDesign {

    clearCompileErrors(){
        this.actions().setCompileErrors(null);
    }

    getEditableName(editableId){
        const editable = this.vue.openedDocument.editables.find(editable=>editable.id == editableId);
        if (editable){
            const props = editable.properties["0"];
            if (props){
                let text;
                switch(editable.type){
                    case EDITABLE_TYPES.API:
                        text =  `${props.method} ${props.path}`
                        break;
                    case EDITABLE_TYPES.DOCUMENT_LEVEL:
                        text =  "All Requests";
                        break;
                    default:
                        text =  props.name;
                        break;
                }
                return props.group ? `${text} (${props.group})`: text;
            }
        }
        return editableId;
    }
}